import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms'; // <-- NgModel lives here
import { UserService } from '../appShared/user.service';
import { ValueService } from '../appShared/value.services';
import { DoktorService } from '../appShared/doktor.service';
import { PacijentiService } from '../appShared/pacijenti.service';

import { AppComponent } from './app.component';
import { MaComponent } from './ma/ma.component';
import { AboutComponent } from './about/about.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeComponent } from './home/home.component';
import { HeroDetailsComponent } from './hero-details/hero-details.component';
import { TechnologyComponent } from './technology/technology.component';
import { DesignComponent } from './design/design.component';
import { CultureComponent } from './culture/culture.component';
import { BusinessComponent } from './business/business.component';
import { PoliticsComponent } from './politics/politics.component';
import { OpinionComponent } from './opinion/opinion.component';
import { TravelComponent } from './travel/travel.component';
import { LoginComponent } from './login/login.component';
import { DoctorComponent } from './doctor/doctor.component';
import { PreglediComponent } from './pregledi/pregledi.component';

import { routing } from './app.routes';

@NgModule({
  declarations: [
    AppComponent,
    MaComponent,
    AboutComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    HeroDetailsComponent,
    TechnologyComponent,
    DesignComponent,
    CultureComponent,
    BusinessComponent,
    PoliticsComponent,
    OpinionComponent,
    TravelComponent,
    LoginComponent,
    DoctorComponent,
    PreglediComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    routing,
    FormsModule
  ],
  providers: [ UserService,
    ValueService,
    PacijentiService,
    DoktorService
],
  bootstrap: [AppComponent]
})
export class AppModule { }



