import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { ValueService } from '../../appShared/value.services';
import {  PairBonusRule, BonusRule, TaxRule } from '../../appShared/interfaces/recepti.interfaces';
@Component({
  selector: 'app-doctor',
  templateUrl: './doctor.component.html',
  styleUrls: ['./doctor.component.less']
})

export class DoctorComponent implements OnInit {

  constructor( private valueService: ValueService) { }
  doctors: any[];
  private valueSubscription: Subscription;
  private bonusSubscription: Subscription;
  public bonusRules: Array<TaxRule>;
  public showData = false;
  public serial = '';
  ngOnInit() {

    this.getDoctors();
  }

  private getDoctors(): void {
    this.valueSubscription = this.valueService.GetBonusRules().subscribe(status => {
        // this.bonusRules = status;

        this.showData = true;
    });
}
onSelectedChange(value: number) {
  // do something else with the value
  console.log(value);

  // rememberto update the selectedValue

}
}



