import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs/Observable';
import {  Pacijenti } from '../appShared/interfaces/pregledi.interfaces';

@Injectable()
export class PacijentiService {

  private baseUrl: string = window.location.origin;
    /**
     * Constructor.
     *
     * @param {Http} private http
     * @param {ConstantsService} private constants
     * @param {LogService} private logger
     */
    constructor(private http: Http) {

    }
    public PacijentiGet(): Observable<Array<Pacijenti>> {

      const headers = new Headers({ 'Accept': 'application/json'
     });
      const options = new RequestOptions({ headers: headers });
      return this.http
          .get('http://localhost:9699/api/v1/pacients/pacijenti', options).pipe(
            map(response => extractData(response)));

            function extractData(response: Response) {
              if (response.status !== 200) {
                  return;
              }

              const data = response.json().recepti;

              if (!data.length) {
                  return;
              }

              let rules: Array<Pacijenti> = [];

              if (data.length) {
                  rules = data;
              }

              return rules;
          }
  }
}




