import { Component, OnInit } from '@angular/core';
import { NgModule } from '@angular/core';
import { ValueService } from '../../appShared/value.services';
import {  Doktori, Pacijenti, ReceptiDto, Dijagnoza, Pregled, Book} from '../../appShared/interfaces/pregledi.interfaces';

@Component({
  selector: 'app-pregledi',
  templateUrl: './pregledi.component.html',
  styleUrls: ['./pregledi.component.less']
})

export class PreglediComponent implements OnInit {

  constructor( private valueService: ValueService) { }
  model: any = {};
  public rece = {} as ReceptiDto;
  myModel = {
    username: '' ,
    password: ''
  };
  errorMessage: String;
  lijekName: String;
  book = {} as Book;

  ngOnInit() {

  }
  private reset() {
    this.book.kolicina = null;
    this.book.lijek = null;
    this.errorMessage = null;
    this.lijekName = null;
    this.model.username = null;
    this.book.opisUpotrebe = null;
}

login(form) {
  this.book.lijek = form.username,
  this.book.opisUpotrebe = form.password,
  this.book.kolicina = 1;
  this.valueService.addRecepte(this.book);
  this.valueService.addBookWithObservable(this.book).subscribe(data => console.log(data));
}

// addBook(): void {
//   this.valueService.addBookWithObservable(this.book)
//     .subscribe( book => {
//       this.reset();
//       this.lijekName = book.lijek;
//     },
//     error => this.errorMessage = <any>error);
// }
}



