import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Http, Response } from '@angular/http';
import { map, catchError } from 'rxjs/operators';
import { Observable } from 'rxjs/Observable';
import { User } from './user.model';

@Injectable()
export class UserService {

   private baseUrl: 'http://localhost:38454/api/values/5';

   readonly rootUrl = 'http://localhost:38454/api/values/';
    /**
     * Constructor.
     *
     * @param {Http} private http
     * @param {ConstantsService} private constants
     * @param {LogService} private logger
     */
    constructor(private http: Http, private https: HttpClient) {

    }

    /**
     * True if is authorized, otherwise false.
     */

    public search(): Observable<string[]> {
      return this.http.get('http://localhost:38454/api/values').pipe(
        map(res => {
          if (res.json().length > 0) {
          return res.json();
          }
         }
        ));
    }
    private logResponse(response: Response) {
        // console.log(response);
        // this.log.debug(response); // ovo ne radi
    }

    private extractData(response: Response) {
        // api ce vratiti clanski broj, ako jeste
        return response.json().length > 0;
    }

}




